import React, { useRef, useEffect, useState } from 'react'
import { Illustration, Ellipse, Rect, Shape, useRender, Box, Cylinder, Anchor } from 'react-zdog'
// New react-spring target, for native animation outside of React
import './styles.css'

/** --- Basic, re-usable shapes -------------------------- */
const TAU = Math.PI * 2

/** --- Assembly ----------------------------------------- */
function Guy() {
	// Change motion every second
	// Turn static values into animated values
	let spinDirection = 'left'
	const ref = useRef()
	let t = 0
	useRender(() => {
		if ((ref.current.rotate.y < 0.4) && (spinDirection === 'left')) {
			ref.current.rotate.y += 0.01
			ref.current.rotate.z += 0.01
		} else if (ref.current.rotate.y > -0.4) {
			spinDirection = 'right'
			ref.current.rotate.y -= 0.01
			ref.current.rotate.z -= 0.01

		} else if (ref.current.rotate.y < -0.4) {
			spinDirection = 'left'
			ref.current.rotate.y -= 0.01
			ref.current.rotate.z -= 0.01

		}

		// ref.current.rotate.y = Math.cos((t += 0.1) / TAU)
		// ref.current.rotate.x = Math.sin((t += 0.1) / TAU)
	})
	return (
		<Anchor ref={ref}>
				{/* нос */}
				<Ellipse
					width={20}
					height={23}
					stroke={1}
					fill={true}

					color={'red'}
					translate={{ y: -3.5 }}
					rotate={{ x: TAU / 4 }}
				/>
				<Rect
					width={0.75}
					height={0.75}
					stroke={0.75}
					translate={{ y: -2, x: -2, z: 4 }}
					color={'black'}
				/>
				<Rect
					width={0.75}
					height={0.75}
					stroke={0.75}
					translate={{ y: -2, x: 2, z: 4 }}
					color={'black'}
				/>
				<Shape
					path={[
						{ x: 0, y: -1 },
						{ x: 1, y: 1 },
						{ x: -1, y: 1 },
					]}
					stroke={0.75}
					translate={{ y: 0, x: 0, z: 3.5 }}
					rotate={{ x: TAU / 16, y: TAU / 4 }}
					color={'#636'}
				/>
				{/* голова */}


				{/* шапка/плоский низ */}
				<Box
					width={8}
					height={6}
					depth={6}
					stroke={false}
					color={'#C25'}
					leftFace={'#EA0'}
					rightFace={'#E62'}
					topFace={'#ED0'}
					bottomFace={'#636'}
					translate={{ y: 0 }}
				/>
				
				{/* квадрат верх */}
				<Cylinder
					diameter={8}
					length={5}
					stroke={false}
					color={'#C25'}
					backface={'#E62'}
					translate={{ y: -6.5 }}
					rotate={{ x: TAU / 4 }}

				/>




		</Anchor>

	)
}


export default function Index() {
	
	return (
		<Illustration
			onDragStart={(pointer) => {
				console.log(`Drag started at ${pointer.pageX}, ${pointer.pageY}`)
			}}
			zoom={10}
			style={{ width: 300, height: 300 }}
		>
			<Guy />
		</Illustration>
	)
}